@font-face {
  font-family: 'Exo 2.0';
  src: url('../public/fonts/Exo20-Thin.eot');
  src: url('../public/fonts/Exo20-Thin.eot?#iefix') format('embedded-opentype'),
    url('../public/fonts/Exo20-Thin.woff2') format('woff2'),
    url('../public/fonts/Exo20-Thin.woff') format('woff'),
    url('../public/fonts/Exo20-Thin.ttf') format('truetype'),
    url('../public/fonts/Exo20-Thin.svg#Exo2.0-Thin') format('svg');
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Exo 2.0';
  font-size: 16px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.container {
  max-width: 1120px;
  margin: 0 auto;
}

h1 {
  font-size: 36px;
  margin-bottom: 26px;
}

.cat_food__container {
  background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 50%, #000000 100%), url(../public/img/background.jpg);
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
